import * as React from 'react';
import styled from 'styled-components';

const StyledDiv = styled.div`
  div {
    width: 50px;
    background: #fff;
    height: 5px;
    margin: 12px 8px;
    position: relative;
    transition: transform 0.5s 0s, top 0.5s 0.25s, opacity 0.5s 0.25s;
    top: 0;
  }
  &.open {
    div {
      transition: transform 0.5s 0.25s, top 0.5s 0s, opacity 0.5s 0s;
    }
    div:nth-child(1) {
      transform: rotateZ(45deg);
      top: 17px;
    }
    div:nth-child(2) {
      opacity: 0;
    }
    div:nth-child(3) {
      transform: rotateZ(-45deg);
      top: -17px;
    }
  }
`;

const MenuButton = ({ classes }) => {
  return (
    <StyledDiv className={`menuButton ${classes}`}>
      <div></div>
      <div></div>
      <div></div>
    </StyledDiv>
  );
};

export default MenuButton;
