import * as React from 'react';
import { useState } from 'react';
import Nav from './Nav';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Image from 'gatsby-plugin-sanity-image';
import MenuButton from './MenuButton';
import styled from 'styled-components';

const StyledHeader = styled.header`
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  > div {
    display: flex;
    justify-content: space-between;
    div{
      img{
        background-color:#fff;
        padding:10px;
      }
    }
    }
  }
  #menuButtonContainer {
    position: relative;
    z-index: 99;
    background: #222;
    padding: 10px;
    border: none;
  }
  #navContainer {
    position: fixed;
    top: 0;
    transition: left 0.2s;
    width: 100vw;
    z-index: -1;
  }
  #navContainer.closed {
    left: 100%;
  }
  #navContainer.open {
    left: 0;
  }
`;

const Header = () => {
  const data = useStaticQuery(graphql`
    query LogoQuery {
      allSanityLogo {
        nodes {
          image {
            ...Image
          }
          name
        }
      }
    }
  `);
  const [OpenNav, setOpenNav] = useState(false);
  const toggleNav = () => {
    setOpenNav(!OpenNav);
  };
  return (
    <StyledHeader>
      <div>
        <div>
          <Link to='/'>
            <Image
              {...data.allSanityLogo.nodes[1].image}
              width={200}
              style={{ height: '100%' }}
              alt={data.allSanityLogo.nodes[1].name}
            />
          </Link>
        </div>
        <button id='menuButtonContainer' onClick={toggleNav}>
          <MenuButton classes={OpenNav ? 'open' : ''} />
        </button>
      </div>
      <div id='navContainer' className={OpenNav ? 'open' : 'closed'}>
        <Nav />
      </div>
    </StyledHeader>
  );
};

export default Header;
