import * as React from 'react';
import { useState } from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import styled from 'styled-components';

const StyledNav = styled.nav`
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: flex-end;
  width: 100%;
  padding-top: 100px;
  background: #fff;
  ul {
    list-style-type: none;
    a {
      text-decoration: none;
      color: #000;
      padding: 10px 20px;
      display: block;
    }
  }
  .secondaryMenu {
    background: #eee;
    width: 100%;
  }
  .mainMenu {
    display: block;
    position: sticky;
    top: 0;
    width: 100%;
    height: 100vh;
    background: #ddd;
    > li {
      display: grid;
      grid-template-columns: 1fr 1fr;
      width: 50%;
    }
  }
  .subMenuTrigger {
    background: none;
    color: #000;
    border: none;
    padding: 10px 20px;
    text-align: left;
  }
  .subMenu {
    height: 0;
    width: 0;
    overflow: hidden;
    position: absolute;
    left: 50%;
    top: 0px;
    background: #eee;
    .subMenuTitle {
      padding: 10px 20px;
      color: #000;
    }
  }
  .subMenu.open {
    height: 100vh;
    width: 50%;
    overflow: auto;
  }
  @media screen and (max-width:900px) {
    grid-template-columns: 1fr;
    .mainMenu {
    > li {
      grid-template-columns: 1fr 1fr;
      width: 100%;
    }
  }
`;

const Nav = () => {
  const [openSubmenu, setOpenSubmenu] = useState('');

  const data = useStaticQuery(graphql`
    query navQuery {
      allSanitySecondaryMenu {
        nodes {
          name
          slug {
            current
          }
          url
        }
      }
      allSanityMainMenu {
        nodes {
          name
          slug {
            current
          }
          subMenu {
            name
            slug {
              current
            }
          }
        }
      }
    }
  `);

  const toggleSubMenu = (menuItem) => {
    if (openSubmenu !== menuItem) {
      setOpenSubmenu(menuItem);
    } else {
      setOpenSubmenu('');
    }
  };

  return (
    <StyledNav>
      <ul className='secondaryMenu'>
        {data.allSanitySecondaryMenu.nodes.map((node) => {
          return (
            <li key={node.slug.current}>
              <a href={node.url}>{node.name}</a>
            </li>
          );
        })}
      </ul>
      <ul className='mainMenu'>
        <li>
          <Link to='/'>Home</Link>
        </li>
        {data.allSanityMainMenu.nodes.map((node) => {
          return (
            <li key={node.slug.current}>
              <button
                className='subMenuTrigger'
                onClick={() => {
                  toggleSubMenu(node.slug.current);
                }}
              >
                {node.name}
              </button>
              <ul
                className={
                  openSubmenu === node.slug.current ? 'open subMenu' : 'subMenu'
                }
              >
                <h3 className='subMenuTitle'>{node.name}</h3>
                {node.subMenu.map((menuItem) => {
                  return (
                    <li key={menuItem.slug.current} className='subMenuItem'>
                      <Link to={`/${menuItem.slug.current}`}>
                        {menuItem.name}
                      </Link>
                    </li>
                  );
                })}
              </ul>
            </li>
          );
        })}
      </ul>
    </StyledNav>
  );
};

export default Nav;
